import { Term } from './term'

export class Regex extends Term {

    constructor(field: string, value: any) {
        super(field, value);
        this.name = 'regexp';
    }

    public static make(field: string, value: any): Regex {
        return new Regex(field, value);
    }

    public flags(value: string): this {
        return this.set('flags', value);
    }
}